import styled from "styled-components"

export const Wrapper = styled.div`

    a {
        background: ${props => props.theme.button.background.normal};
        border: var(--line-width) solid ${props => props.theme.button.border.normal};
        border-radius: 2px;
        color: ${props => props.theme.button.text.normal};
        display: inline-block;
        line-height: var(--line-height-tight);
        margin-bottom: 4px;
        padding: 17px 32px 15px;
        position: relative;
        text-decoration: none;
        user-select: none;

        &:hover { 
            background: ${props => props.theme.button.background.hover};
            border: var(--line-width) solid ${props => props.theme.button.border.hover};
            color: ${props => props.theme.button.text.hover};    
        }
    
    }

`